/* ----------FOOTER---------- */
.footer {
    box-sizing: border-box;
    background-color: rgb(125, 149, 226);
    color: #ffffff;
    text-align: center;
    font-size: 10px;
    font-family: Futura;
    padding: 10px;
}

@media only screen and (min-width: 768px) {
    /* For desktop: */
    .footer {font-size: 12px}
}

@media only screen and (min-width: 980px) {
    .footer {font-size: 15px}
}
  