/* ----------CONTENt: ABOUT---------- */
.main-div {
    background-color: white;
}

.row::after {
    content: "";
    clear: both;
    display: table;
}
  
[class*="col-"] {
    float: left;
    padding: 15px;
}
  
/* For mobile phones: */
[class*="col-"] {
    width: 100%;
}

.about-tittle{
    text-align: center;
}

#profile {
    font-family: Roboto;
    font-size: xx-large;
}

#sub-profile {
    font-family: Futura;
    font-size:small;
}

.circle-profile {
    padding-top: 50px;
    padding-bottom: 50px;
    border-radius: 50%;
    max-height: 350px;
    display: block;
    margin: auto;
}

#about-me {
    font-family: Roboto;
    text-align: left;
    font-size: x-large;
}

#desc-about-me {
    font-family: Futura;
    text-align: left;
    font-size:large;
    line-height: 2.5;
}

#details {
    font-family: Roboto;
    text-align: left;
    font-size: x-large;
}

.desc-details {
    font-family: Futura;
    text-align: left;
    font-size:large;
    line-height: 2.5;
}

.social-icon {
    max-height: 50px;
}





/* ----------MEDIA ADJUST---------- */
@media only screen and (min-width: 600px) {
    /* For tablets: */
    .col-s-1 {width: 8.33%;}
    .col-s-2 {width: 16.66%;}
    .col-s-3 {width: 25%;}
    .col-s-4 {width: 33.33%;}
    .col-s-5 {width: 41.66%;}
    .col-s-6 {width: 50%;}
    .col-s-7 {width: 58.33%;}
    .col-s-8 {width: 66.66%;}
    .col-s-9 {width: 75%;}
    .col-s-10 {width: 83.33%;}
    .col-s-11 {width: 91.66%;}
    .col-s-12 {width: 100%;}
  
    #sub-profile {font-size:medium;}
  
    #about-me {padding-left: 10px; padding-right: 10px; text-align: center;}
    #desc-about-me {padding-left: 10px; padding-right: 10px; text-align: center;}
  
    #details {padding-left: 10px; padding-right: 10px; text-align: center;}
    .desc-details {padding-left: 10px; padding-right: 10px; text-align: center;}
}

@media only screen and (min-width: 768px) {
    /* For desktop: */
    .col-1 {width: 8.33%;}
    .col-2 {width: 16.66%;}
    .col-3 {width: 25%;}
    .col-4 {width: 33.33%;}
    .col-5 {width: 41.66%;}
    .col-6 {width: 50%;}
    .col-7 {width: 58.33%;}
    .col-8 {width: 66.66%;}
    .col-9 {width: 75%;}
    .col-10 {width: 83.33%;}
    .col-11 {width: 91.66%;}
    .col-12 {width: 100%;}
  
    #sub-profile {font-size:large;}
  
    #about-me {padding-left: 30px; padding-right: 10px;}
    #desc-about-me {padding-left: 30px; padding-right: 10px;}
  
    #details {padding-left: 10px; padding-right: 30px;}
    .desc-details {padding-left: 10px; padding-right: 30px;}
} 